
.page{background-color:#fff;width:100%;height:100%;position:absolute;top:0;left:0
}
.page.slide-left-enter{left:100%
}
.page.slide-left-enter-active{transition:left .3s
}
.page.slide-left-leave-active{left:-100%;transition:left .3s
}
.page.slide-right-enter{left:-100%
}
.page.slide-right-enter-active{transition:left .3s
}
.page.slide-right-leave-active{left:100%;transition:left .3s
}


