
li{position:relative;font-size:.8em;height:1.88em;color:#afe2f6;background-color:#3cb8ea;text-shadow:0 0 1px #093c51
}
li .number{position:absolute;left:.3em;top:.3em
}
li .label{position:absolute;top:.2em;left:2em;font-size:.8em
}
li .extra{position:absolute;top:2.2em;left:3em;font-size:.5em;font-style:italic
}
li .extra:before{content:'"'
}
li .extra:after{content:'"'
}
li.active{color:#fff;background-color:#1381ad;text-shadow:0 0 1px #041a23
}
li .icon{width:1.3em;height:1.3em;position:absolute;top:.3em;right:.3em
}
li .icon svg *{stroke:currentColor;fill:transparent;stroke-width:5;-webkit-filter:drop-shadow(0 0 2px #093c51);filter:drop-shadow(0 0 2px #093c51)
}


