
button.nav-button{background-color:#d6975c;color:#fff;position:relative;width:100%;padding:.2em;text-shadow:0 0 1px #523214
}
button.nav-button .button-main{text-transform:uppercase
}
button.nav-button .button-info{line-height:.7em;font-size:.5em
}
button.nav-button.active{background-color:#a36429;text-shadow:#140c05
}
button.nav-button .chevron{width:21px;position:absolute;right:21px;top:50%;margin-top:-18px
}
button.nav-button .chevron svg{-webkit-filter:drop-shadow(0 0 1px #523214);filter:drop-shadow(0 0 1px #523214)
}
button.nav-button .chevron svg path{fill:currentColor
}
button.nav-button .chevron.left{left:10.5px
}
button.nav-button .chevron.left svg path{transform:scale(-1, 1)
}
button.nav-button .chevron.right{right:10.5px
}


